.navbar{
    display: flex;
    justify-content: space-around;
    box-shadow: 0px 1px 3px -2px black;
    align-items: center;
}

.nav-logo{
    display: flex;
    align-items: center;
    gap: 10px;
    width: 50px;
}

.nav-logo p{
    color: black;
    font-size: 38px;
    font-weight: 600;
}

.nav-menu{
display: flex;
align-items: center;
list-style: none;
gap: 50px;
color: red;
font-size: 20px;
font-weight: 500;
margin: 10px 100px;


}

.nav-menu li{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 3px;
    cursor: pointer;
    
    
}
.nav-menu a {
    color:#172D13; 
}

.nav-menu hr{
    border: none;
    width: 80%;
    height: 3px;
    border-radius: 10px;
    background: #172D13;
}

.nav-login-cart{
    display: flex;
    align-items: center;
    gap: 45px

}

.nav-login-cart button{
    width: 157px;
    height: 58px;
    outline: none;
    border: 1px solid #7a7a7a;
    border-radius: 75px;
    color: #172D13;
    font-size: 20px;
    font-weight: 500;
    background:white;
    cursor: pointer;
    border: 3px solid #172D13;
    
}

.nav-login-cart button:active{
    background: #f3f3f3;
}

.nav-cart-count{
    width: 22px;
    height: 22px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -35px;
    margin-left: -55px;
    border-radius: 11px;
    font-size: 14px;
    background: #172D13;
    color: white;
}

.menu_dropdown{
    display: none;
}

@media(max-width:1280px){
    .navbar{
        padding: 12px 50px;
    }

    .nav-logo img{
        width: 40px;
    }

    .nav-logo p{
        font-size: 25px;
    }

    .nav-menu{
        gap: 30px;
        font-size: 16px;
    }

    .nav-login-cart{
        gap: 30px;

    }

    .nav-login-cart button{
        width: 120px;
        height: 45px;
        font-size: 16px;
    }

    .nav-cart-count{
        margin-left: -40px;
        font-size: 12px;
    }
}

@media(max-width: 1024px){
    .navbar{
        padding: 12px 30px;
    }

    .nav-menu{
        gap: 25px;
        font-size: 14px;

    }

    .nav-login-cart button{
        width: 80px;
        height: 35px;
        font-size: 14px;
    }

    .nav-login-cart img{
        width: 30px;
    }

    .nav-cart-count{
        width: 18px;
        height: 18px;
    }
}


@media(max-width: 800px){
    .navbar{
        padding: 10px 0px;
    }

    .menu_dropdown{
        display: block;
        width: 30px;
        transition: 0.5s;
      margin-left: 30%;
        

    }
    
    .nav-menu{
        display: none;
        height: 80px;
        width: 100%;
        position: absolute;
        background: white;
        justify-content: center;
        top: 50px;
        
    }

    .nav-menu-visible{
        display: flex;
        margin-top: 60px;
        
    }

    .menu_dropdown open{
        transition: 0.5s;
       
        
    }

    
    }



@media(max-width: 500px){
    .navbar{
        padding: 8px 0px;
        gap: 0px;

    }

    .nav-logo{
        transform: scale(0.8);
    }

    .nav-menu{
        height: 70px;
        top: 50px;
    }
    .nav-login-cart{
        transform: scale(0.8);
    }
}
